.them_card{background:white;border-radius:8px;-webkit-box-shadow:0px 0px 15px rgba(0,0,0,0.08);box-shadow:0px 0px 15px rgba(0,0,0,0.08)}

.menu_row>.item{padding:15px 0}

.tag_group{background:white}

.tag_group .item{-webkit-box-flex:1;-ms-flex:1;flex:1;text-align:center;position:relative;padding:10px;font-size:14px}

.tag_group .item.active{color:#1b8db2}

.tag_group .item.active::after{content:'';position:absolute;bottom:0px;height:4px;width:80px;background:#1b8db2;left:50%;margin-left:-40px;border-radius:10px}

.tag_group.fixed{position:fixed;top:46px;width:100%;background:white;z-index:11}

.them_btn_bottom{background:white;position:fixed;width:100%;z-index:11;bottom:0;left:0;padding:10px}

.them_btn_bottom .them_btn{width:90%;margin:0 auto}

.form_group .input_group{padding:0px 0;margin-bottom:20px;position:relative}

.form_group .input_group .lable{margin-bottom:5px}

.form_group .input_group .input_info{padding:10px 0;font-size:12px;color:#999}

.form_group .input{position:relative}

.form_group .input input{-webkit-box-flex:1;-ms-flex:1;flex:1;outline:none;border-radius:0;background:none;border:none;padding:15px 0;font-size:15px;padding-right:10px;font-weight:bold;color:#333}

.form_group .input input:focus ~ hr{border:none;position:absolute;bottom:0;left:0;width:100%;height:1px;background:#1b8db2}

.form_group .input input::-webkit-input-placeholder{font-weight:normal}

.form_group .input hr{border:none;position:absolute;bottom:0;left:0;width:100%;height:1px;background:#f3f4f6}

.form_group .input .do{min-width:60px;margin-left:10px;border-left:1px solid #e8e8e8;padding-left:10px}

.form_group .submit{display:block;margin:20px auto;width:100%}

.form_group .info{margin:30px 0;color:#999;font-size:12px}

.van-overlay .main{position:relative}

.van-overlay .main .title{font-size:18px;font-weight:bold;margin:20px 0}

.van-overlay .main .close{border:1px solid #eb716e;position:absolute;right:20px;top:20px;width:40px;height:40px;color:#eb716e;text-align:center;line-height:40px;border-radius:100px}

.choost_type>span{padding:5px 12px;border:1px solid #e8e8e8;color:#999;border-radius:100px;margin-left:5px;font-size:12px}

.choost_type>span.active{background:#1b8db2;color:white;border:1px solid #1b8db2;-webkit-box-shadow:0px 0px 10px #f390ba;box-shadow:0px 0px 10px #f390ba}

.noData{text-align:center;padding:20px;color:#8f9eab}

.noData .van-icon{opacity:0.6;display:block;font-size:50px;margin-top:30px;margin-bottom:10px}

.noData .van-icon::after{display:block;font-size:14px;margin-top:20px}

.noData .van-icon.zh_cn::after{content:'\6682\65E0\6570\636E'}

.noData .van-icon.zh_en::after{content:'No Data'}

.noData .van-icon.zh_hk::after{content:'\66AB\7121\6578\64DA'}

.noData.table_null{margin-top:20px}

.copyIcon{display:inline-block;width:15px;height:15px;vertical-align:middle;background:url(data:image/png;base64,) no-repeat;background-size:100% 100%;opacity:0.5}
